<template>
	<view class="template-login">
		<view class="login">
			<!-- 顶部背景图片-->
			<view class="login__bg login__bg--top">
				<image class="bg" src="https://tnuiimage.tnkjapp.com/login/2/login-top2.png" mode="widthFix"></image>
			</view>

			<view class="login__wrapper">
				<view class="tn-margin-left tn-margin-right tn-text-bold" style="font-size: 60rpx;">
					欢迎回来
				</view>
				<view class="tn-margin tn-color-orange--disabled tn-text-lg">
					乡村振兴，我看行！
				</view>

				<!-- 输入框内容-->
				<view class="login__info tn-flex tn-flex-direction-column tn-flex-col-center tn-flex-row-center">
					<!-- 登录 -->
					<block v-if="currentModeIndex === 0">
						<view
							class="login__info__item__input tn-flex tn-flex-direction-row tn-flex-nowrap tn-flex-col-center tn-flex-row-left">
							<view class="login__info__item__input__left-icon">
								<view class="tn-icon-phone"></view>
							</view>
							<view class="login__info__item__input__content">
								<!-- <input maxlength="20" :value="username" placeholder-class="input-placeholder" placeholder="请输入用户名" /> -->
								<tn-input v-model="username" placeholder="请输入用户名"
									placeholder-class="input-placeholder" />
							</view>
						</view>

						<view
							class="login__info__item__input tn-flex tn-flex-direction-row tn-flex-nowrap tn-flex-col-center tn-flex-row-left">
							<view class="login__info__item__input__left-icon">
								<view class="tn-icon-safe"></view>
							</view>
							<view
								class="login__info__item__input__content login__info__item__input__content--verify-code">
								<!-- <input placeholder-class="input-placeholder" :value="password" placeholder="请输入密码" /> -->
								<tn-input v-model="password" placeholder="请输入密码" type="password"
									placeholder-class="input-placeholder" />
							</view>
							<!--  <view class="login__info__item__input__right-verify-code" @tap.stop="getCode">
                <tn-button backgroundColor="#01BEFF" fontColor="#FFFFFF" size="sm" padding="5rpx 10rpx" width="100%" shape="round">{{ tips }}</tn-button>
              </view> -->
						</view>
					</block>
					<!-- 注册 -->
					<block v-if="currentModeIndex === 1">
						<view
							class="login__info__item__input tn-flex tn-flex-direction-row tn-flex-nowrap tn-flex-col-center tn-flex-row-left">
							<view class="login__info__item__input__left-icon">
								<view class="tn-icon-phone"></view>
							</view>
							<view class="login__info__item__input__content">
								<!-- <input maxlength="20" v-model:value="username" placeholder-class="input-placeholder"
									placeholder="请输入用户名" /> -->
								<tn-input v-model="username" placeholder="请输入用户名"
									placeholder-class="input-placeholder" />
							</view>
						</view>

						<view
							class="login__info__item__input tn-flex tn-flex-direction-row tn-flex-nowrap tn-flex-col-center tn-flex-row-left">
							<view class="login__info__item__input__left-icon">
								<view class="tn-icon-safe"></view>
							</view>
							<view
								class="login__info__item__input__content login__info__item__input__content--verify-code">
								<input placeholder-class="input-placeholder" type="password" :value="password"
									placeholder="请输入密码" />
							</view>
							<!-- <view class="login__info__item__input__right-verify-code" @tap.stop="getCode">
                <tn-button size="sm" padding="5rpx 10rpx" width="100%" shape="round">{{ tips }}</tn-button>
              </view> -->
						</view>

						<view
							class="login__info__item__input tn-flex tn-flex-direction-row tn-flex-nowrap tn-flex-col-center tn-flex-row-left">
							<view class="login__info__item__input__left-icon">
								<view class="tn-icon-lock"></view>
							</view>
							<view class="login__info__item__input__content">
								<input v-model="password" :password="!showPassword" type="password"
									placeholder-class="input-placeholder" placeholder="请再次输入登录密码" />
								<!-- 	<tn-input :type="type">
										
									</tn-input> -->
							</view>
							<view class="login__info__item__input__right-icon" @click="showPassword = !showPassword">
								<view :class="[showPassword ? 'tn-icon-eye' : 'tn-icon-eye-hide']"></view>
							</view>
						</view>
					</block>

					<view class="login__info__item__button tn-bg-blue tn-color-white" hover-class="tn-hover"
						:hover-stay-time="150" @click="currentModeIndex === 0 ? login():insert()">
						{{ currentModeIndex === 0 ? '登录' : '注册'}}
					</view>


					<view v-if="currentModeIndex === 1" :class="[{'login__info__item__tips': currentModeIndex === 0}]">
						<view class="tn-flex tn-flex-row-between tn-padding">
							<view class="" @tap.stop="modeSwitch(0)">前往登录</view>
						</view>
					</view>
					<view v-if="currentModeIndex === 0" :class="[{'login__info__item__tips': currentModeIndex === 1}]">
						<view class="tn-flex tn-flex-row-between tn-padding">
							<view class="tn-padding-right" @tap.stop="modeSwitch(1)">账号注册</view>
							<view class="tn-padding-left tn-color-gray">忘记密码</view>
						</view>
					</view>

				</view>

				<!-- 其他登录方式 -->
				<view class="login__way tn-flex tn-flex-col-center tn-flex-row-center">
					<view class="tn-padding-sm tn-margin-xs">
						<view
							class="login__way__item--icon tn-flex tn-flex-row-center tn-flex-col-center tn-color-teal--dark">
							<view class="tn-icon-wechat-fill"></view>
						</view>
					</view>
					<view class="tn-padding-sm tn-margin-xs">
						<view class="login__way__item--icon tn-flex tn-flex-row-center tn-flex-col-center tn-color-red">
							<view class="tn-icon-sina"></view>
						</view>
					</view>
					<view class="tn-padding-sm tn-margin-xs">
						<view
							class="login__way__item--icon tn-flex tn-flex-row-center tn-flex-col-center tn-color-blue">
							<view class="tn-icon-qq"></view>
						</view>
					</view>
				</view>
			</view>

			<!-- 底部背景图片-->
			<view class="login__bg login__bg--bottom">
				<image src="https://tnuiimage.tnkjapp.com/login/2/login-bottom2.png" mode="widthFix"></image>
			</view>

		</view>

		<!-- 验证码倒计时 -->
		<tn-verification-code ref="code" uniqueKey="login-demo-4" :seconds="60" @change="codeChange">
		</tn-verification-code>
		<tn-toast ref="toast"></tn-toast>
	</view>

</template>

<script>
	import template_page_mixin from '@/libs/mixin/template_page_mixin.js'
	export default {
		name: 'login-demo-4',
		mixins: [template_page_mixin],
		data() {
			return {
				username: "",
				password: "",
				// 当前选中的模式
				currentModeIndex: 0,
				// 模式选中滑块
				modeSliderStyle: {
					left: 0
				},
				// 是否显示密码
				showPassword: false,
				// 倒计时提示文字
				tips: '获取验证码'
			}
		},
		watch: {
			currentModeIndex(value) {
				const sliderWidth = uni.upx2px(476 / 2)
				this.modeSliderStyle.left = `${sliderWidth * value}px`
			}
		},
		methods: {
			login() {
				let that = this;
				const requestTask = uni.request({
					url: 'http://www.rural.abc/user/userlogin', //仅为示例，并非真实接口地址。
					method: 'POST',
					header: {
						'custom-header': 'hello', //自定义请求头信息
						'content-type': "application/x-www-form-urlencoded"
					},
					data: {
						name: that.$data.username,
						password: that.$data.password
					},
					success: function(res) {
						console.log(res)
						if (res.data.statue) {
							that.$refs.toast.show({
								title: '登录成功',
								content: '',
								icon: 'success',
								image: '',
								duration: 2000,
							});
							uni.setStorage({
								key: 'token',
								data: res.data.token,

							});
							uni.setStorage({
								key: 'name',
								data: res.data.user.name,

							});
							uni.setStorage({
								key: 'image',
								data: res.data.user.image,
								success: function() {
									uni.redirectTo({
										url: '/pages/index/index'
									});
								}
							});
						} else {
							that.$refs.toast.show({
								title: '登录失败',
								content: '请检查密码或者用户名',
								icon: 'close-circle',
								image: '',
								duration: 1500
							})
						}

					},
					fail: function(res) {
						that.$refs.toast.show({
							title: '登录失败',
							content: '请检网络',
							icon: 'close-circle',
							image: '',
							duration: 1500
						})
					}
				});
			},
			insert(e) {
				let that = this
				uni.request({
					url: "http://www.rural.abc/user/insert",
					method: "POST",
					data: {
						name: that.$data.username,
						password: that.$data.password
					},
					header: {
						'custom-header': 'hello', //自定义请求头信息
						'content-type': "application/x-www-form-urlencoded"
					},
					success: function(res) {
						if (res.data.result == 1) {
							that.$refs.toast.show({
								title: '注册成功',
								content: '',
								icon: 'success',
								image: '',
								duration: 2000,
							});

							that.$data.currentModeIndex = 0;
						} else {
							that.$refs.toast.show({
								title: '登录失败',
								content: '请检查密码或者用户名',
								icon: 'close-circle',
								image: '',
								duration: 1500
							});
						}
					},

					fail: function(res) {
						that.$refs.toast.show({
							title: '登录失败',
							content: '请检网络',
							icon: 'close-circle',
							image: '',
							duration: 1500
						})

					}
				})
			},
			// 切换模式
			modeSwitch(index) {
				this.currentModeIndex = index
				this.showPassword = false
			},
			codeChange(event) {
				this.tips = event
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '@/static/css/templatePage/custom_nav_bar.scss';
	/* 悬浮 */
	/* .login-sussuspension{
    animation: suspension 3s ease-in-out infinite;
  }
  
  @keyframes suspension {
    0%, 100% {
      transform: translate(0 , 0);
    }
    50% {
      transform: translate(0rem , 1rem);
    }
  } */

	.login {
		position: relative;
		height: 100%;
		z-index: 1;

		/* 背景图片 start */
		&__bg {
			z-index: -1;
			position: fixed;

			&--top {
				top: 0;
				left: 0;
				right: 0;
				width: 100%;

				.bg {
					width: 750rpx;
					will-change: transform;
				}
			}

			&--bottom {
				bottom: -10rpx;
				left: 0;
				right: 0;
				width: 100%;
				// height: 144px;
				margin-bottom: env(safe-area-inset-bottom);

				image {
					width: 750rpx;
					will-change: transform;
				}
			}
		}

		/* 背景图片 end */

		/* 内容 start */
		&__wrapper {
			margin-top: 300rpx;
			width: 100%;
		}

		/* 切换 start */
		&__mode {
			position: relative;
			margin: 0 auto;
			width: 476rpx;
			height: 77rpx;
			margin-top: 100rpx;
			background-color: rgba(255, 255, 255, 0.6);
			box-shadow: 0rpx 10rpx 50rpx 0rpx rgba(0, 3, 72, 0.1);
			border-radius: 39rpx;

			&__item {
				height: 77rpx;
				width: 100%;
				line-height: 77rpx;
				text-align: center;
				font-size: 31rpx;
				color: #080808;
				letter-spacing: 1em;
				text-indent: 1em;
				z-index: 2;
				transition: all 0.4s;

				&--active {
					font-weight: bold;
					color: #FFFFFF;
				}
			}

			&__slider {
				position: absolute;
				height: inherit;
				width: calc(476rpx / 2);
				border-radius: inherit;
				box-shadow: 0rpx 18rpx 72rpx 18rpx rgba(0, 195, 255, 0.1);
				z-index: 1;
				transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
			}
		}

		/* 切换 end */

		/* 登录注册信息 start */
		&__info {
			margin: 80rpx 30rpx 10rpx 30rpx;
			padding-bottom: 0;
			border-radius: 20rpx;

			&__item {

				&__input {
					margin-top: 59rpx;
					width: 100%;
					height: 77rpx;
					border: 1rpx solid #E6E6E6;
					border-radius: 39rpx;

					&__left-icon {
						width: 10%;
						font-size: 44rpx;
						margin-left: 20rpx;
						color: #838383;
					}

					&__content {
						width: 80%;
						padding-left: 10rpx;

						&--verify-code {
							width: 56%;
						}

						input {
							font-size: 24rpx;
							// letter-spacing: 0.1em;
						}
					}

					&__right-icon {
						width: 10%;
						font-size: 44rpx;
						margin-right: 20rpx;
						color: #838383;
					}

					&__right-verify-code {
						width: 34%;
						margin-right: 20rpx;
					}
				}

				&__button {
					margin-top: 75rpx;
					margin-bottom: 39rpx;
					width: 100%;
					height: 77rpx;
					text-align: center;
					font-size: 31rpx;
					font-weight: bold;
					line-height: 77rpx;
					letter-spacing: 1em;
					text-indent: 1em;
					border-radius: 39rpx;
					box-shadow: 1rpx 10rpx 24rpx 0rpx rgba(60, 129, 254, 0.35);
				}

				&__tips {
					margin: 30rpx 0;
					color: #AAAAAA;
				}
			}
		}

		/* 登录注册信息 end */

		/* 登录方式切换 start */
		&__way {
			margin: 0 auto;
			margin-top: 110rpx;

			&__item {
				&--icon {
					width: 85rpx;
					height: 85rpx;
					font-size: 80rpx;
					// border-radius: 100rpx;
					margin-bottom: 18rpx;
					position: relative;
					z-index: 1;

					// &::after {
					//   content: " ";
					//   position: absolute;
					//   z-index: -1;
					//   width: 100%;
					//   height: 100%;
					//   left: 0;
					//   bottom: 0;
					//   border-radius: inherit;
					//   opacity: 1;
					//   transform: scale(1, 1);
					//   background-size: 100% 100%;
					//   background-image: url(https://tnuiimage.tnkjapp.com/cool_bg_image/icon_bg5.png);
					// }
				}
			}
		}

		/* 登录方式切换 end */
		/* 内容 end */

	}

	/deep/.input-placeholder {
		font-size: 24rpx;
		color: #838383;
	}
</style>